import { cn } from "@/lib/utils";
import type { ReactNode } from "react";

interface PixelTextProps {
  children: ReactNode;
  className?: string;
}

/**
 * PixelText - Simula lo stile voxel/pixel dell'icona Michelangelo
 * con ombra 3D isometrica e bordi netti
 */
export function PixelText({ children, className }: PixelTextProps) {
  return (
    <span
      className={cn(
        "relative inline-block font-bold tracking-tight",
        "bg-gradient-to-r from-[#6b8cff] via-[#8b5cf6] to-[#a855f7]",
        "bg-clip-text text-transparent",
        className
      )}
      style={{
        fontFamily: "'Inter', 'SF Mono', 'Monaco', monospace",
        // Ombra 3D voxel-style che simula l'icona
        textShadow: `
          1px 1px 0 rgba(107, 140, 255, 0.4),
          2px 2px 0 rgba(107, 140, 255, 0.3),
          3px 3px 0 rgba(107, 140, 255, 0.2),
          4px 4px 0 rgba(107, 140, 255, 0.1),
          5px 5px 0 rgba(107, 140, 255, 0.05),
          6px 6px 12px rgba(107, 140, 255, 0.3)
        `,
        WebkitTextStroke: "1px rgba(107, 140, 255, 0.3)",
        transform: "translateZ(0)",
        willChange: "transform",
      }}
    >
      {children}
    </span>
  );
}

/**
 * PixelTextSolid - Versione con effetto voxel pi marcato
 * per uso su sfondi scuri (simula l'icona bianca su nero)
 */
export function PixelTextSolid({ children, className }: PixelTextProps) {
  return (
    <span
      className={cn(
        "relative inline-block font-bold tracking-tight",
        className
      )}
      style={{
        fontFamily: "'Inter', 'SF Mono', 'Monaco', monospace",
        color: "#ffffff",
        // Ombra 3D stile voxel che sporge in basso-destra
        textShadow: `
          1px 1px 0 #6b8cff,
          2px 2px 0 #5a7bf7,
          3px 3px 0 #4a6af0,
          4px 4px 0 #3a59e8,
          5px 5px 0 #2a48e0,
          6px 6px 0 #1a37d8,
          7px 7px 0 #0a26d0,
          8px 8px 20px rgba(107, 140, 255, 0.5)
        `,
        transform: "translateZ(0) translate(-2px, -2px)",
        willChange: "transform",
      }}
    >
      {children}
    </span>
  );
}

/**
 * PixelTextOutline - Versione outline con effetto pixel
 * simile agli strumenti bianchi dell'icona
 */
export function PixelTextOutline({ children, className }: PixelTextProps) {
  return (
    <span
      className={cn(
        "relative inline-block font-bold tracking-tight",
        className
      )}
      style={{
        fontFamily: "'Inter', 'SF Mono', 'Monaco', monospace",
        color: "transparent",
        WebkitTextStroke: "2px #ffffff",
        // Ombra 3D grigia che simula l'effetto voxel
        textShadow: `
          2px 2px 0 rgba(0, 0, 0, 0.8),
          4px 4px 0 rgba(0, 0, 0, 0.6),
          6px 6px 0 rgba(0, 0, 0, 0.4),
          8px 8px 0 rgba(0, 0, 0, 0.2),
          10px 10px 20px rgba(0, 0, 0, 0.5)
        `,
        transform: "translateZ(0)",
        willChange: "transform",
      }}
    >
      {children}
    </span>
  );
}
